(function() {
  if(Meteor.isCordova){
    var Agent = function() {
      var self = this;
      //init
      navigator.unicallAppInfo.initial();
      //**********************************

      //basic function
      this.addEventListener=function(type,callback){
        navigator.unicallAppInfo.addEventListener(type,callback);
      }
      this.removeEventListener=function(type,callback){
        navigator.unicallAppInfo.removeEventListener(type,callback);
      }
      this.sendMessage=function(type,msg){
        navigator.unicallAppInfo.sendMessage(type,msg);
      }
      //**********************************

      //extend function

      this.livechatLoadReady=function(){
        self.sendMessage("livechat-sdk-ready",{time:new Date()});
      }
      this.registerCallbackOnSignReady=function(callback){
        self.addEventListener("appInfo",callback);
      }

      //**********************************

    };
    var target=null;
    this.getUnicallAppInfoAgent=function(){
      if(target === null){
        target = new Agent();
      }
      return target;
    }
  }

}).call(this);
